import React, { Component, Fragment } from "react";
import { Link } from "react-router-dom";
import { withTranslation } from "react-i18next";
import { CenterContainer } from "@util-components";
import { RegisterWrapper, RegisterPanel, PanelHeader, PanelBody, Actions } from "./register.style";
import { ProviderItem } from "./children";

type Provider = {};

type Register = {
	provider: String
};

type Props = {
	providers: Array<Provider>,
	t: Function
};

type State = {
	register: Register,
	canContinue: false
};

class RegisterComponent extends Component<Props, State> {
	constructor(props) {
		super(props);
		this.state = {
			canContinue: false,
			register: {
				provider: ""
			}
		};
	}

	next = () => {
		const { canContinue, register: { provider } } = this.state;
		const { protocol, host } = window.location;
		if (canContinue) {
			window.location = `${provider}?returnToUrl=${protocol}//${host}/`;
		}
	};

	selectProvider = (e) => {
		const { register } = this.state;
		this.setState({
			register: { ...register, provider: e.target.value },
			canContinue: true
		});
	};

	onSubmit = (e) => {
		e.preventDefault();
		const { canContinue, register: { provider } } = this.state;
		const { protocol, host } = window.location;
		if (canContinue) {
			window.location = `${provider}?returnToUrl=${protocol}//${host}/`;
		}
	};

	render() {
		const { canContinue, register: { provider } } = this.state;
		const { providers, t } = this.props;

		return (
			<RegisterWrapper id="register-wrapper" data-testid="register-wrapper">
				<CenterContainer>
					<form onSubmit={this.onSubmit}>
						<RegisterPanel className="register-panel">
							<h1 data-testid="title">{t("register.title")}</h1>
							<PanelHeader className="panel-header" data-testid="panel-header">
								<h2>{t("register.step1Title")}</h2>
								<hr />
							</PanelHeader>
							<PanelBody className="panel-body">
								<Fragment>
									<a
										href="https://solid.inrupt.com/how-it-works"
										target="_blank"
										rel="noopener noreferrer"
									>
										{t("register.whatIsAProvider")}
									</a>
									<Link to="/login" className="a-with-spacing">
										{t("register.alreadySolid")}
									</Link>
									<ul>
										{providers.map((providerData) => (
											<ProviderItem
												data={providerData}
												key={providerData.id}
												onSelect={this.selectProvider}
												radioName="providerRadio"
												id={`radio-${providerData.id}`}
												checked={providerData.registerLink === provider}
											/>
										))}
									</ul>
								</Fragment>
							</PanelBody>
							<Actions className="actions">
								<button className="btn-solid" onClick={this.next} type="submit" disabled={!canContinue}>
									{t("register.next")}
								</button>
							</Actions>
						</RegisterPanel>
					</form>
				</CenterContainer>
			</RegisterWrapper>
		);
	}
}

export { RegisterComponent };
export default withTranslation()(RegisterComponent);
